.land {
  margin: 0;
  padding: 0;
  font-family: "Arial", sans-serif;
}

.land-header {
  padding-top: 50px;
  background-size: 80%;
  background-repeat: no-repeat;
  background-position: center;
}

.land-header__main {
  margin-top: 50px;
  margin-bottom: 200px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;

  @media (min-width: @tablet-width) {
    flex-wrap: nowrap;
  }
}

.land-header__container {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: space-between;
  margin: 0 10% 0 5%;
}

.land-header__photo {
  width: 100%;
  margin: 10% 0 10% 0;

  @media (min-width: @desktop-width) {
    margin: 15% 0 10% 0;
  }
}

.land__icon {
  width: 50%;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.land-header__icon {
  width: 20%;

  @media (max-width: 500px) {
    width: 50%;
  }
}

.land__icon-heading {
  text-align: center;
  color: #83CD81;
}

.land-header__heading {
  font-size: 23px;
  margin: 5%;

  @media (min-width: @desktop-width) {
    position: absolute;
    font-size: 63px;
    margin: 100px 0 0 40px;
  }
}

.land-header__mini-container {
  width: 20%;
  flex-shrink: 1;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-around;
}

.land-header__button {
  text-decoration: none;
  display: block;
  margin-left: 15px;
  margin-right: auto;
  text-align: center;
  width: 20%;
  padding: 8px;
  font-size: 16px;
  border: 2px solid rgba(255,167,3,1);
  border-radius: 10px;
  color: rgba(255,167,3,1);
  font-weight: bold;
  text-transform: uppercase;

  @media (min-width: @desktop-width) {
    font-size: 22px;
    width: 500px;
    padding: 17px;
    text-align: center;
  }
}

.land-header__button:hover {
  cursor: pointer;
  background: rgba(255,167,3,1);
  color: #FFFFFF;
}

.land-header__button--register {
}

.land-header__add-container {
  text-align: center;
  margin: 20px 20px 0 20px;
  background-color: #F3F3F3;
  padding: 10px;
  border-radius: 10%;

  @media (min-width: @desktop-width) {
    width: 20vw;
    margin: 0;
  }
}

.landos {
  @media (min-width: @desktop-width) {
    width: 30%;
  }
}

.up {
  margin-top: 90px;
}
